/* This style sheet contains all styled global classes
  Specific styles are overwritten in modules specific to each component
*/

/* Hide footer elements when keyboard visible*/
@media screen and (max-height: 300px) {
  .footer {
    position: relative;
    visibility: hidden;
    display: none;
    height: 10px;
  }
  .content {
    min-height: 300px;
  }
  .footer .divider {
    visibility: hidden;
  }
  .main__button {
    visibility: hidden;
  }
  .alt__button {
    visibility: hidden;
  }
}

* {
  /* CSS Reset*/
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body,
html {
  font-family: "Raleway", sans-serif;
  line-height: 1.6em;
}

:root {
  --primary: hsl(219, 37%, 33%);

  --secondary: #49c1a2;
  --secondary-dark: #37a08e;
  --danger: #ff4c46;
  --shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.2);
}
input {
  font-family: "Raleway", sans-serif;
  text-align: center;
}

.header,
.footer,
.content {
  position: absolute;
  right: 0;
  left: 0;
}

.header {
  height: 75px;
  top: 0;
  background-color: hsl(219, 37%, 33%);
  background-color: var(--primary);
  color: #fff;
  padding: 20px;
  text-align: center;
}

.header i {
  font-size: 1.5em;
  position: absolute;
  top: 25px;
  right: 25px;
}

/*Footer */
.footer {
  height: 75px;
  bottom: 0;
  background-color: hsl(219, 37%, 33%);
  background-color: var(--primary);
  color: #fff;
  font-size: 2em;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
}

.iconContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  width: 100%;
}
.dividerOverlay {
  position: absolute;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: right;
  width: 100%;
  visibility: hidden;
}
.divider {
  color: #49c1a2;
  color: var(--secondary);
  margin-right: -4px; /* Line up with grid */
  visibility: visible;
}

/*Content*/
.content {
  top: 75px;
  bottom: 75px; /* 150px for workout page, maybe*/
  overflow: auto;
  max-width: 900px;
  margin: 0 auto;
}
.contentHeading {
  height: 50px;
  text-align: center;
  padding: 20px;
  color: #49c1a2;
  color: var(--secondary);
}

/* Main Button*/
.main__button {
  position: fixed;
  bottom: 85px;
  right: calc(15px);
  font-size: 1.5em;
  color: white;
  border-radius: 90px;
  padding: 20px 25px 20px 25px;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.2);
  box-shadow: var(--shadow);
}

.alt__button {
  position: fixed;
  left: 15px;
  bottom: 85px;
  font-size: 1.5em;
  color: white;
  border-radius: 90px;
  padding: 20px 25px 20px 25px;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.2);
  box-shadow: var(--shadow);
}
a {
  color: white;
  cursor: pointer;
}

.green {
  background-color: #49c1a2;
  background-color: var(--secondary);
}

.start i {
  color: white;
}

.red {
  background-color: #ff4c46;
  background-color: var(--danger);
}

.container {
  text-align: center;
  /* margin-top: 15px; */
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}
.container > h2 {
  color: #49c1a2;
  color: var(--secondary);
}

.card {
  display: grid;
  padding: 10px 0px 10px 10px;
  border: 1px solid #cccccc;
  border-radius: 5px;
  margin: 15px 15px 0 15px;
  background-color: #fff;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.2);
  box-shadow: var(--shadow);
}

.card__header {
  display: grid;
  justify-items: center;
  grid-template-columns: 1fr 8fr 1fr;
}

.card__title {
  border: none;
  font-size: 1.5rem;
  text-align: center;
  font-family: "Raleway";
  font-weight: bold;
}

.card__body table {
  width: 100%;
}
tbody {
  text-align: center;
}

.card__body {
  padding: 10px 10px 10px 0;
}

.card__footer {
  display: grid;
  grid-template-columns: 1fr;
}

button {
  color: white;
  border: 1px solid #fff;
  border-radius: 5px;
  font-size: 0.8em;

  background-color: white;
  color: #49c1a2;
  color: var(--secondary);
}

button i {
  margin-right: 5px;
}

.card__edit {
}
.card__delete {
  color: #ff4c46;
  color: var(--danger);
}

