.form {
  padding: 10px;
}

.container {
  padding-right: 10px;
}

.container input {
  margin-bottom: 20px;
  border-bottom: 1px solid rgb(120, 120, 120);
  padding: 10px;
  display: grid;
}

.container input::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.exerciseForm {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
}
.notClickable {
  background-color: gainsboro;
}

.icon {
  /*realign icon*/
  margin-left: -3px;
}

.link {
  display: inline-block;
  padding: 1em;
  margin: -1em;
  border-radius: 90px;
}
